import { processStyle } from './processStyle';export function getStyles(componentStyles = {}) {  const styleKeys = Object.keys(componentStyles);  // Return only inlineStyles to be attached to the component  return styleKeys.reduce((acum, key) => {    const isFunction = typeof componentStyles[key] === 'function';    return {      ...acum,      [key]: isFunction        ? (...args) => processStyle(null, componentStyles[key](...args))        : processStyle(null, componentStyles[key]),    };  }, {});}