import styles from '../../styles';import { getFinalProps } from './getFinalProps';import { getStore } from './getStore';export default (isBrowser, Store) => componentDef => {  const registerNumber = Store.methods.registerComponent(componentDef);  const processedClasses = styles.getClasses(registerNumber, componentDef.classes);  // Provoque Side Effect for classes styles  styles.registerStyles(isBrowser, registerNumber, processedClasses.rules);  const store = getStore(Store, registerNumber);  const processedStyles = styles.getStyles(componentDef.styles);  const solveProps = getFinalProps({    store,    componentDef,    classesMapping: processedClasses.mapping || {},    processedStyles,  });  return function renderComponent(props, children) {    // Called on inital render and every update    const render = () => {      const finalProps = solveProps(props);      const rendered = componentDef.render(finalProps, children);      return rendered;    };    // console.log('-------------------', component);    const rendered = render();    return rendered;  };};