export function registerStyles(isBrowser, registerNumber, styledRules) {  if (!styledRules) return;  if (isBrowser) {    const node = document.getElementById('component-styles');    const alreadyRegistered = node.innerHTML.indexOf(`--${registerNumber}`) > -1;    if (!alreadyRegistered) node.innerHTML += styledRules;  } else {    // global.__componentStyles used in SSR Server Side Rendering    if (!global.__componentStyles) global.__componentStyles = '';    global.__componentStyles += styledRules;  }}