export function getStore(Store, registerNumber) {  return {    alerts: Store.objects.alerts,    check: Store.methods.check,    toggle: Store.methods.toggle,    measure: Store.utils.measure,    set: Store.methods.set,    setItem: Store.methods.setItem,    call: Store.process.call,    get: Store.methods.get(registerNumber),    db: Store.db ? Store.db.query : () => undefined,  };}