export function getFinalProps(args) {  const {    store,    componentDef,    classesMapping,    processedStyles,  } = args;  const {    state = () => ({}),    actions = () => ({}),  } = componentDef;  const classes = {    ...Object.keys(classesMapping).reduce((acum, key) => {      return { ...acum, [key]: classesMapping[key] };    }, {}),  };  return (props = {}) => {    return {      props,      state: state(props, store),      actions: actions(props, store),      styles: processedStyles,      classes,    };  };}