import { processStyle } from './processStyle';export function getClasses(registerNumber, componentStyles = {}) {  const styleKeys = Object.keys(componentStyles);  const mapping = {};  const rules = styleKeys.reduce((acum, key) => {    const newClassName = `${key}--${registerNumber}`;    mapping[key] = newClassName;    return `${acum}${processStyle(newClassName, componentStyles[key])}`;  }, '');  return {    rules,    mapping,  };}